import { useState } from 'react';
import { useNormalizedOptions } from '../hooks';
/**
 * useFilterState
 * @param options
 * @param keysToFilterBy
 * @returns {}
 */
export const useFilterState = ({ defaultValue = '', keysToFilterBy = ['value', 'label'], options: rawOptions, }) => {
    const options = useNormalizedOptions(rawOptions);
    const [filterValue, setFilterValue] = useState(String(defaultValue));
    const searchBy = filterValue.toLowerCase();
    const filteredOptions = options.filter((option) => {
        let found = false;
        for (const key of keysToFilterBy) {
            if (String(option[key] || '')
                .toLowerCase()
                .includes(searchBy)) {
                found = true;
                break;
            }
        }
        return found;
    });
    return {
        filterValue,
        filteredOptions,
        setFilterValue,
    };
};
