import { useContext } from 'react';
import { ScopeContext } from '../Context.js';

/* ----------------------- useScope ----------------------- */

function useScope(name) {
  var scope = useContext(ScopeContext); // Example
  // scope = "friends[0]"
  // name  = "friends[0]"
  // return "friends[0]"

  if (scope === name) {
    return name;
  } // Example
  // scope = "friends[0]"
  // name  = "name"
  // return "friends[0].name"


  if (scope && name) {
    return "".concat(scope, ".").concat(name);
  }

  return name;
}

export { useScope };
