import { getDaysArray } from '@tesla/design-system/dist/components/date-picker';
import { useMemo } from 'react';
/**
 * useCalendarGrid
 * - Generate a 7x6 grid of dates representing the current month of the given date
 * @param {Date} date - A date to build the month grid around
 * @return {Date[]} days - An array of dates representing the current month
 */
export const useCalendarGrid = (date) => {
    const days = useMemo(() => getDaysArray(date), [date]);
    return days;
};
